import React from 'react'
import { Link } from 'react-router-dom'
export const menuList = [
    { name: '', ref: "/womazing-/" },
    { name: '', ref: "/shop" },
    { name: ' ', ref: "/brands" },
    { name: '', ref: "/contact" },
]

export const MenuList = () => {
    return (
        <ul>

            {menuList.map((obj, i) =>
                <li key={i} >
                    <Link to={obj.ref}>{obj.name}</Link>
                </li>)
            }
        </ul >
    )
}
